// stylelint-disable at-rule-empty-line-before
@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";

//картинки
$img_dir: "../images/";

.pd-0 {
	padding: 0 !important;
}
.develop-container {
	margin-bottom: 13px;
}
.row-develop {
	margin: 0 -10px;
	display: flex;
	flex-wrap: wrap;
	box-sizing: border-box;
}
.develop-content {
	width: 100%;
	display: block;
}
.row-develop div,
.row-develop a,
.row-develop span {
	box-sizing: border-box;	
}
.develop-col {
	display: flex;
	padding: 0 10px;
	width: 33.33333333%;
	margin: 10px 0;
}
.develop-box {
	display: flex;	
	position: relative;
	border: 1px solid #E6E6E6;
	text-decoration: none !important;
	background: #FFF;
	width: 100%;
	padding-bottom: 66px;
	overflow: hidden;
}
.develop-box-img {
	width: 100%;
	display: block;
}
.develop-box-img img {
	width: 100%;
}
.develop-box-content {
	position: absolute;
	left: 0;
	z-index: 2;
	background: #FFF;
	bottom: 0;
	width: 100%;
	padding: 27px 25px 15px;
	display: flex;		
	flex-direction: column;
	border-top: 1px solid #E6E6E6;
	height: 230px;
	transform: translateY(160px);
	transition: transform 0.3s;	
}
.develop-box-title {
	display: block;
	width: 100%;
	text-transform: uppercase;
	color: #464646 !important;
	font: 13px/16px "OpenSansBold";
	margin-bottom: 10px;
}
.develop-box-p {
	display: block;
	width: 100%;
	font: 13px/19px "OpenSansRegular";
	color: #464646 !important;
	width: 100%;
	margin-bottom: 17px;
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 6;	
	max-height: 113px;
	flex: 1;
}
.develop-box-link {
	font: 13px/19px "OpenSansRegular";
	color: #568DEA !important;
	text-decoration: underline;
}
.develop-box-link:hover {
	text-decoration: none;
}

.develop-box-txt {
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s, visibility 0.3s;		
	height: 100%;
	display: flex;		
	flex-direction: column;
}
.develop-box:hover .develop-box-content {
	transform: translateY(0);
}
.develop-box:hover .develop-box-txt {
	visibility: visible;
	opacity: 1;
}
.bx-touch .develop-box:hover .develop-box-content {
	transform: translateY(160px);	
}
.bx-touch .develop-box:hover .develop-box-txt {
	opacity: 0;
	visibility: hidden;	
}

.help-info-row {
	display: flex;
	margin: 0 -10px;
}
.help-info-col {
	padding: 0 10px;
}
.help-col-4 {
	width: 33.33333333%;
}
.help-col-8 {
	width: 66.66666667%;
}
.help-info-box {
	.big-heading {
		text-align: left;
		margin-top: 12px;
		margin-bottom: 7px;
	}
		p {
		color: #000;
		padding-right: 80px;
		margin-bottom: 50px;
	}
}
.help-info-img {
	display: block;
	width: 100%;
	
	img {
		width: 100%;
		border: 1px solid #E6E6E6;
	}
}
.button-green {
	background: #72A234;
	-webkit-box-shadow: 0 4px 0 #5c8428;
	box-shadow: 0 4px 0 #5c8428;
	text-decoration: none !important;
	border: 0 !important;
	
	&:hover {
		span {
			text-decoration: underline !important;
			border: 0 !important;		
		}		
	}
	&:active {
		span {
			text-decoration: none !important;
			border: 0 !important;		
		}		
	}
}
.content-info-wrap {
	padding: 55px 0 65px;
	background: #f7f6f6;
	
	div, span, a, h2 {
		box-sizing: border-box;
	}
}
.content-wrap-partner {
	padding-top: 65px;
}
.main-header {
	overflow: hidden;
}


$feature: max-width;

$mediaWidth1: 991px;
$mediaWidth2: 768px;
$mediaWidth3: 767px;
$mediaWidth4: 639px;
$mediaWidth5: 479px;


/*#{$mediaWidth1}*/ //-991px
@media ($feature: $mediaWidth1) {
.develop-col {
 	width: 325px;
 }
 .develop-content {
		display: flex;
		justify-content: center;
 }
 .row-develop {
 	width: 650px;
 }
 .our-features-brief-text br {
 	display: none;
 }
 .help-info-box p {
 	padding-right: 0;
 }
 .main-nav__item {
 	padding: 0 50px;
 }	
 .brief-about-us__header br {
 	display: none;
 }
	.content-wrap-text-simple {
		.our-features-brief {
			margin-bottom: 0;
			padding-bottom: 0;
		}
	} 
}
/*END #{$mediaWidth1}*/ //-991px


/*#{$mediaWidth2}*/ //-768px
@media ($feature: $mediaWidth2) {
	.help-info-row {
		margin: 0;
		flex-direction: column;
		justify-content: center;
	}
	.help-col-4 {
		width: 100%;
	}
	.help-info-img {
		max-width: 305px;
		width: 100%;
		margin: 0 auto;
		display: block;
	}
	.help-col-8 {
		width: 100%;
	}
	.help-info-box {
		p  {
			margin-bottom: 30px;
		}
		.big-heading {
			br {
				display: none;
			}
		}
	}
	.content-info-wrap {
		padding: 20px 0 40px;
		box-sizing: border-box;
	}
	.row-develop {
		width: 100%;
		margin: 0;
		justify-content: center;
	}
	.develop-col {
		max-width: 325px;
		width: 100%;
	}
	p {
		font-size: 15px;
		line-height: 18px;
	}
}/*END #{$mediaWidth2}*/ //-768px


